@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");

html,
body {
  margin: 0 auto;
  background: black;
  color: #00b800;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  font-family: "Press Start 2P", sans-serif;
  text-align: center;
}

body {
  font-weight: 400;
}

button {
  font-family: "Press Start 2P", sans-serif;
  display: block;
  margin: 0 auto;
  border: none;
  background: #a80020;
  background: #00b800;
  padding: 10px 20px;
  text-transform: uppercase;
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
}

h1 {
  font-size: 20px;
  text-align: center;
}

textarea {
  font-family: "Press Start 2P", sans-serif;
  border: 3px solid black;
  width: 90%;
  margin: 0 auto;
  outline: none;
  height: 300px;
  background: #00b800;
  padding: 10px;
  width: 50%;
}

textarea:disabled {
  background-color: #a5a2a2;
}
